import React, { Fragment, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import {
  Route,
  Switch,
  useLocation,
} from "react-router-dom";

import Fifth from './fifth';
import FifthA from './fifthA';
import First from './first';
import FirstA from './firstA';
import Fourth from './fourth';
import FourthA from './fourthA';
import Home from './home';
import Result from './result';
import ResultAni from './resultAnimation';
import Second from './second';
import SecondA from './secondA';
import Seven from './seven';
import SevenA from './sevenA';
import Sixth from './sixth';
import SixthA from './sixthA';
import Third from './third';
import ThirdA from './thirdA';


function Routing() {

  const location = useLocation();


  return (
    <Fragment>
      <div>
        <Switch location={location}>
          <Route path="/one" children={<First />}/>
          <Route path="/oneA">
            <FirstA />
          </Route>
          <Route path="/two">
            <Second />
          </Route>
          <Route path="/twoA">
            <SecondA />
          </Route>
          <Route path="/three">
            <Third />
          </Route>
          <Route path="/threeA">
            <ThirdA />
          </Route>
          <Route path="/four">
            <Fourth />
          </Route>
          <Route path="/fourA">
            <FourthA />
          </Route>
          <Route path="/five">
            <Fifth />
          </Route>
          <Route path="/fiveA">
            <FifthA />
          </Route>
          <Route path="/six">
            <Sixth />
          </Route>
          <Route path="/sixA">
            <SixthA />
          </Route>
          <Route path="/seven">
            <Seven />
          </Route>
          {<ResultAni />}
          <Route path="/">
            <Home />
          </ Route>
        </ Switch>
      </div>
    </ Fragment>
  )
}


export default withRouter(Routing);